<template>
  <ul class="search-bar">
    <li><label></label></li>
    <li>
      <Select v-model="d_value.auditStatus" style="width:200px" placeholder="" clearable>
        <Option v-for="item in statusList" :value="item.id" :key="item.id">{{ item.name }}</Option>
      </Select>
    </li>
    <li>
      <Button type="ghost" class="search-btn" @click="searchBar"></Button>
    </li>
  </ul>
</template>
<script>
  import {formatDate} from "@/js/util";
  export default {
    props: {
      value: {
        type: Object,
        default () {
          return {};
        }
      }
    },
    data() {
      return {
        d_value:this.value,
        statusList:[{id:0,name:""},{id:1,name:""},{id:9,name:""}],
      }
    },
    methods: {
      searchBar() {
        this.$emit('on-search');
      },
    },
    watch:{
      d_value(val){
        this.$emit('input',val);
      }
    },
    mounted(){
    }
  };
</script>



