<template>
  <div>
    <Form ref="form" :model="formData" :rules="readonly?{}:formRules" :label-width="80">
      <FormItem prop="name" label="" required>
        <Input v-model="formData.name" placeholder="" :readonly="readonly"></Input>
      </FormItem>
      <FormItem prop="businessLicences" label="" required>
        <Input v-model="formData.businessLicences" placeholder="" :readonly="readonly"></Input>
      </FormItem>
      <Row>
        <Col span="12">
        <FormItem prop="contacts" label="" required>
          <Input v-model="formData.contacts" placeholder="" :readonly="readonly"></Input>
        </FormItem>
        </Col>
        <Col span="1">&nbsp;</Col>
        <Col span="11">
        <FormItem prop="telephone" label="" required>
          <Input v-model="formData.telephone" placeholder="" :readonly="readonly"></Input>
        </FormItem>
        </Col>
      </Row>
      <FormItem prop="platformName" label="" required>
        <Input v-model="formData.platformName" placeholder="" :readonly="readonly"></Input>
      </FormItem>
      <FormItem prop="platformDes" label="" required>
        <Input v-model="formData.platformDes" placeholder="" type="textarea"
               :autosize="{minRows: 2,maxRows: 5}" :readonly="readonly"></Input>
      </FormItem>
      <FormItem prop="platformUrl" label="">
        <Input v-model="formData.platformUrl" placeholder="" :readonly="readonly"></Input>
      </FormItem>
      <FormItem prop="platformParameter" label="">
        <Input v-model="formData.platformParameter" placeholder="" type="textarea"
               :autosize="{minRows: 2,maxRows: 5}" :readonly="readonly" :maxlength="2000"></Input>
      </FormItem>
      <Row v-if="!readonly">
        <Col span="8">&nbsp;</Col>
        <Col span="8">
        <Button type="primary" size="large" @click="submit" long></Button>
        </Col>
        <Col span="8">&nbsp;</Col>
      </Row>
    </Form>
  </div>
</template>
<script type="text/javascript">
  import $http from '@/js/http2';

  export default {
    components: {},
    props: ['formData', 'readonly', 'resetFlag'],
    data() {
      return {
        formRules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          businessLicences: [
            {required: true, message: '', trigger: 'blur'}
          ],
          contacts: [
            {required: true, message: '', trigger: 'blur'}
          ],
          telephone: [
            {required: true, message: '', trigger: 'blur'}
          ],
          platformName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          platformDes: [
            {required: true, message: '', trigger: 'blur'}
          ],
          platformUrl: [
            // {required: true, message: '', trigger: 'blur'}
          ],
          platformParameter: [
            // {required: true, message: '', trigger: 'blur'}
          ]
        },
      };
    },
    methods: {
      submit(name) {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.$emit('on-submit');
          }
        });
      },
      resetForm() {
        this.$refs.form.resetFields();
      },
    },
    mounted() {
    },
    watch: {
      resetFlag(flag) {
        if (flag) {
          this.resetForm();
        }
      }
    }
  };
</script>
<style lang="less">
</style>



